body {
  background-color: #fbdd74;
  color: #294910;
  height: 100vh;
  width: 100vw;
  margin: 0;
  position: relative;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  font-family: Poppins;
  font-weight: 500;
}

h1 {
  font-size: 10vw;
  margin: 0 20px;
  line-height: 1.4;
}
h2 {
  bottom: 0;
  position: fixed;
  font-size: 24px;
  line-height: 1.2;
  margin: 0 20px 20px;
  font-weight: normal;
}

section {
  display: flex;
  height: 100vh;
  justify-content: space-between;
  flex-direction: row;
}

figure {
  background: #ffffff;
  margin: 0;
  position: relative;
  width: 50vw;
}

figcaption {
  font-size: 36px;
  font-weight: normal;
  position: fixed;
  text-align: center;
  z-index: 1;
  bottom: 0;
  color: white;
  margin: 0 auto;
  width: 50%;
  padding: 20px 0;
}

img {
  height: 100vh;
  object-fit: cover;
  position: fixed;
  width: 50%;
  opacity: 0;
}

aside {
  height: 100%;
  width: 100%;
  position: absolute;
  background: #c4c4c4;
  display: flex;
  z-index: 2;
  align-items: center;
  justify-content: center;
}

.loading-bar {
  display: grid;
  grid-row-gap: 12px;
}

.progress {
  border: 1px solid #294910;
  height: 20px;
  width: 400px;
}
/* sudo class below */

progress[value]::-webkit-progress-bar {
  background: #e8e8e8;
}

progress[value]::-webkit-progress-value {
  background: #294910;
}
.hide {
  opacity: 0;
}

.display {
  opacity: 1;
}
@media (max-width: 800px) {
  section {
    flex-direction: column;
  }
  figure {
    flex-grow: 1;
    width: 100vw;
  }
  img {
    width: 100%;
    position: absolute;
  }
  figcaption {
    font-size: 40px;
  }
  h1 {
    font-size: 80px;
  }
  header h2 {
    position: unset;
  }
}
